<template>
  <div class="content">
    <div class="app-container calendar-list-container">
      <div class="filter-container">
          <el-select style="margin-right:10px;" placeholder="" filterable clearable v-model="listQuery.menuClassId"
              @keyup.enter.native="handleFilter">
            <el-option
                v-for="item in menuClass"
                :key="item.id"
                :label="item.menuClassName"
                :value="item.id">
            </el-option>
          </el-select>
 
          <el-button style="margin-right:10px;" class="filter-item" type="primary" icon="search" @click="handleFilter"></el-button>

        <el-button class="filter-item" @click="handleCreate" type="primary" icon="edit">
        </el-button>
      </div>
      <el-table :key='tableKey' :data="list" v-loading="listLoading" element-loading-text="" border fit
                    highlight-current-row style="width: 100%">
        <el-table-column align="center" label="">
            <template slot-scope="scope">
                <span>{{ scope.row.menuClassName }}</span>
            </template>
        </el-table-column> 
        <el-table-column align="center" label="">
            <template slot-scope="scope">
                <span>{{ scope.row.menuName }}</span>
            </template>
        </el-table-column> 
        <el-table-column align="center" label="">
            <template slot-scope="scope">
                <span>{{ scope.row.price }}</span>
            </template>
        </el-table-column> 
        <el-table-column align="center" label="">
            <template slot-scope="scope">
               <img :src="scope.row.menuImg" width="80" height="80" class="head_pic"/>
                <!-- <span><img :src="scope.row.menuImg" alt=""></span> -->
                
            </template>
        </el-table-column> 
        <el-table-column align="center" label="">
            <template slot-scope="scope">
                <span>{{ scope.row.saleNum }}</span>
            </template>
        </el-table-column>  
        <el-table-column align="center" label="">
            <template slot-scope="scope">
                <span>{{ scope.row.introduce }}</span>
            </template>
        </el-table-column> 
        <el-table-column align="center" label="">
            <template slot-scope="scope">
                <span v-if="scope.row.isRecommend == 1"></span>
                <span v-else></span>
            </template>
        </el-table-column>              
        <el-table-column align="center" label="" width="200">
            <template slot-scope="scope">
                <el-button size="small" type="success"
                          @click="handleUpdate(scope.row)">
                </el-button>
                <el-button size="small" type="danger"
                          @click="deleterow(scope.row)">
                </el-button>
                
            </template>
        </el-table-column>
      </el-table>
      <div v-show="!listLoading" class="pagination-container">
        <el-pagination @size-change="handleSizeChange" @current-change="handleCurrentChange"
                      :current-page.sync="listQuery.page"
                      :page-sizes="[10,20,30,50]" :page-size="listQuery.pageSize"
                      layout="total, sizes, prev, pager, next, jumper" :total="total">
        </el-pagination>
      </div>
      <el-dialog :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible">
      <el-form :model="form" :rules="rules" ref="form" label-width="120px">
        <el-form-item v-show="false" label="id" prop="id" v-if="dialogStatus == 'update'">
          <el-input v-model="form.id" placeholder="id" :disabled="true"></el-input>
        </el-form-item>
   
        <el-form-item label="" prop="menuClassId">
          <el-select v-model="form.menuClassId" placeholder="" @change="changeSel">
            <el-option
              v-for="item in menuClass"
              :key="item.id"
              :label="item.menuClassName"
              :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" prop="menuName">
          <el-input v-model="form.menuName" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="price">
          <el-input v-model="form.price" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="menuImg">
          <el-upload
            class="avatar-uploader"
            :action="uploadUrl"
            :headers="headers"
            :show-file-list="false"
            :on-success="handleAvatarSuccess"
            :before-upload="beforeAvatarUpload">
            <img v-if="imageUrl" :src="imageUrl" class="avatar">
            <i v-else class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
          <el-input v-model="form.menuImg" style="display:none"></el-input>
        </el-form-item>
        <el-form-item label="" prop="introduce">
          <el-input type="textarea" v-model="form.introduce" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="isRecommend">
          <el-radio v-model="form.isRecommend" label="1"></el-radio>
          <el-radio v-model="form.isRecommend" label="0"></el-radio>
        </el-form-item>
        
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancel('form')"> </el-button>
        <el-button v-if="dialogStatus=='create'" type="primary" @click="create('form')"> </el-button>
        <el-button v-if="dialogStatus == 'update'" type="primary" @click="update('form')"> </el-button>
      </div>
    </el-dialog>
    </div>
  </div>
</template>

<script>
import axios from '@/router/axios'
import { getToken } from '@/utils/token'
import { baseUrl } from '@/utils/baseUrl'
export default {
  data () {
    return {
      tableName: 'menuTable',
      uploadUrl: '',  //
      tableKey: 0,
      list: null,
      total: null,
      listLoading: true,
      listQuery: {
          page: 1,
          pageSize: 20
      },
      textMap: {
        create: '',
        update: ''
      },
      rules: {
        menuClassId: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        menuClassName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        menuName: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        price: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        menuImg: [
          {
            required: true,
            message: "",
            trigger: "blur"
          }
        ],
        // isRecommend: [
        //   {
        //     required: true,
        //     message: "",
        //     trigger: "blur"
        //   }
        // ],
      },
      form: {
        isRecommend: '0'
      }, 
      // isRecommend: '0',     
      dialogFormVisible: false,
      dialogStatus: '',
      imageUrl: '',
      heads: {},
      menuClass: []
    }
  },
  created() {
    this.uploadUrl = baseUrl() + 'admin/menu/uploadImg'
    this.getList()
    this.getMenuClass()
    const Authorization = getToken()
    this.headers = {Authorization: Authorization}
  },
  methods: {
    /**
     * 
     */
    getList(){
      this.listLoading = true
      for(let key in this.listQuery) {
        if(this.listQuery[key] == '') {
          delete this.listQuery[key]
        }
      }
      axios.get(baseUrl()+'admin/menu/list', { params: this.listQuery }).then(res => {
        if(res.data.code === 0) {
          this.list = res.data.data.rows
          this.total = res.data.data.count
          this.listLoading = false
        }
        else {
          this.$message({
            message: res.data.msg,
            type: 'warning'
          });
        } 
      }).catch(err => {
        this.$message({
            message: '',
            type: 'warning'
          });
      })     
    },
    
    /**
     * 
     */
    handleFilter(){
      this.listQuery.page = 1;
      this.getList();
    },

    /**
     * 
     */
    getMenuClass(){
      axios.get(baseUrl()+'admin/menuClass/alllist').then(res => {
        if(res.data.code === 0) {
          this.menuClass = res.data.data
        }
        else {
          this.$message({
            message: res.data.msg,
            type: 'warning'
          });
        } 
      }).catch(err => {
        this.$message({
            message: '',
            type: 'warning'
          });
      })     
    },

    /**
     * 
     */
    handleSizeChange(val) {
      this.listQuery.pageSize = val
      this.getList()
    },

    /**
     * 
     */
    handleCurrentChange(val) {
        this.listQuery.page = val
        this.getList()
    }, 

    /**
     * 
     */
    handleUpdate(row) {
      console.log(row)
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.form.id = row.id;
      this.form.menuClassId = row.menuClassId;
      this.form.menuClassName = row.menuClassName;
      this.form.menuName = row.menuName;
      this.form.price = row.price;
      this.form.menuImg = row.menuImg;
      this.imageUrl = row.menuImg;
      this.form.introduce = row.introduce;
      this.form.isRecommend = row.isRecommend;
    },

    /**
     * 
     */
    deleterow(row){
      this.$confirm(
        ", ?",
        "",
        {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
        }
      ).then(() => {
        axios.get(baseUrl()+'admin/menu/delete',{params:{id:row.id}})
        .then(response => {
            this.getList();
            this.$notify({
            title: "",
            message: "",
            type: "success",
            duration: 2000
            });
        })
        .catch(() => {
            this.$notify({
            title: "",
            message: "",
            type: "error",
            duration: 2000
            });
        });
      });
    },

    /**
     * 
     */
    handleCreate() {
      this.dialogStatus = "create";
      this.dialogFormVisible = true;
      this.form = {};
      this.imageUrl = ''
    },
    /**
     * 
     */
    create(formName) {
      // this.form.isRecommend = this.isRecommend;
      const set = this.$refs;
      set[formName].validate(valid => {
        if (valid) {
          axios.post(baseUrl()+'admin/menu/add', this.form).then(res => {
            if (res.data.code === 0) {
              this.dialogFormVisible = false;
              this.getList();
              this.$notify({
                title: "",
                message: "",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message.error('');
            }           
          }).catch(err => {
            this.$message.error('');
          });
        } else {
          return false;
        }
      });
    },
    /**
     * 
     */
    cancel(formName) {
      this.dialogFormVisible = false;
      this.form = {};
      const set = this.$refs;
      set[formName].resetFields();
    },
    /**
     * 
     */
    update(formName) {
      // this.form.isRecommend = this.isRecommend;
      console.log(this.form)
      const set = this.$refs;
      set[formName].validate(valid => {
        if (valid) {
          this.dialogFormVisible = false;
          axios.put(baseUrl()+'admin/menu/update', this.form).then(res => {
            if(res.data.code === 0) {
              this.dialogFormVisible = false;
              this.getList();
              this.$notify({
                title: "",
                message: "",
                type: "success",
                duration: 2000
              });
            } else {
              this.$message.error('');
            }
            
          }).catch(err => {
            this.$message.error('');
          });
        } else {
          return false;
        }
      });
    },

    /**
     * 
     */
    handleAvatarSuccess(res, file) {
      if (res.code == 0) {
        this.imageUrl = res.imgUrl;
        this.form.menuImg = res.imgUrl;
        this.$message({
          message: '',
          type: 'success'
        });
      } else {
        this.$message.error('');
      }
      
    },

    /**
     * 
     */
    beforeAvatarUpload(file) {
      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;

      if (!isJPG) {
        this.$message.error(' JPG !');
      }
      if (!isLt2M) {
        this.$message.error(' 2MB!');
      }
      return isJPG && isLt2M;
    },
    
    /**
     * 
     */
    changeSel(menuClassId) {
      for(let i = 0; i < this.menuClass.length; i++) {
        if(this.menuClass[i].id == menuClassId) {
          this.form.menuClassName = this.menuClass[i].menuClassName
          break;
        }
        
      }
    },

    }  
  }

</script>



