import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/views/index/index'
import navBar from '@/components/navBar.vue'
import Login from '@/views/login/index'
import menuClass from '@/views/menuClass/index'
import menu from '@/views/menu/index'
import order from '@/views/order/index'
import { getToken } from '@/utils/token'
const jwt = require('jsonwebtoken')
Vue.use(Router)

const router = new Router({
  routes: [
    {
      path: '/',
      name: '',     
      component: navBar,
      children: [
        { path: 'index', component: order, name: '', meta: {requireAuth: true} },
        { path: 'menuClass', component: menuClass, name: '', meta: {requireAuth: true} },
        { path: 'menulist', component: menu, name: '', meta: {requireAuth: true} }
      ],
      meta: {requireAuth: true},
      redirect:'/index'
    },

    {
      path: '/login',
      name: '',
      component: Login,
      meta: {requireAuth: false}
    },
    { path: '/*', redirect: '/index' }
  ]
})

router.beforeEach((to, from, next) => {
  // console.log(to)
  if (to.matched.some(res => res.meta.requireAuth)) {// 
  if (getToken()) {// 
    jwt.verify(getToken(), 'jwt', (err, decode)=> {
      if (err) {  //   || token
        alert('')
        next({path: '/login',}) 
      } 
      else {
          next()
      }
    })
  } else {// 
   next({
   path: '/login',
  //  query: {redirect: to.fullPath}
   })
  }
  } else {
  next()
  }
 })

 export default router



